.element-navbar__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: 30px;
  padding: 0 2px;
  border: 1px solid black;
}
.element-navbar__search-button {
  width: 20px;
  height: 20px;
  background-image: url(../../assets/images/UIF-121-512.png);
  background-size: cover;
  border: none;
  cursor: pointer;
}

.element-navbar__sort-btn {
  cursor: pointer;

}
.element-navbar__sort-btn-secondary {
  background-color: #a9ff0087;
  cursor: pointer;
}
